@charset "UTF-8";
/* BEM support Func
 -------------------------- */
/* rem support Func
 -------------------------- */
/* BEM
 -------------------------- */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0 none;
  background: transparent;
  font-size: 100%;
  vertical-align: baseline;
  -webkit-tap-highlight-color: transparent;
  outline: none; }

ul,
ol,
menu {
  list-style: none; }

/**
 * Add the correct display in IE 9-.
 */
canvas {
  display: inline-block; }

/**
 * Add the correct display in IE.
 */
template {
  display: none; }

/* Hidden
   ========================================================================== */
/**
 * Add the correct display in IE 10-.
 */
[hidden] {
  display: none; }

[v-cloak] {
  display: none; }

.clearfix {
  *zoom: 1; }

.clearfix:after {
  display: block;
  clear: both;
  content: ' ';
  visibility: hidden;
  height: 0; }

.dn {
  display: none; }

.d-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: center no-repeat url(https://qiniu-cdn7.jinxidao.com/m/images/loading.gif);
  background-size: 32px;
  z-index: 888888; }

body {
  font-family: "Arial","Microsoft YaHei","\9ED1\4F53","\5B8B\4F53",sans-serif; }
